import React, { Component } from 'react';
import d3 from 'd3';
import { getWidth, getHeight } from '../../utilities';

export default class TrendChart extends Component {
  shouldComponentUpdate() {
    return false;
  }
  componentWillReceiveProps(nextProps) {
    if (!nextProps.data) {
      return;
    }
    this.updateChart(nextProps);
  }
  componentDidMount() {
    this.createChart();
  }
  componentWillUnmount() {
    d3.select(this.refs.trendChart).remove();
  }
  render() {
    const style = {
      position: 'relative',
      height: '100%',
    };
    return <div style={style} ref="trendChart" />;
  }

  createChart() {
    let onBrushEnd;
    let filterStatus;
    const chartElement = this.refs.trendChart;
    this.chart = trendChart()
      .width(getWidth(chartElement))
      .height(getHeight(chartElement));

    this.updateChart = function(nextProps) {
      filterStatus = nextProps.filterStatus;
      onBrushEnd = nextProps.onBrushEnd;
      const data = nextProps.data || [];
      let dataset = data.map(function(d) {
        if (d.group && d.group.constructor === Array) {
          d.group = d.group[0];
        }
        return d;
      });

      this.chart.width(getWidth(chartElement)).height(getHeight(chartElement));

      d3
        .select(chartElement)
        .datum(dataset)
        .call(this.chart);
    };

    function trendChart() {
      let svg = null;
      let brushExtent = [90, 360];
      let bars = null;
      let widgetHeight = 500;
      let widgetWidth = 500;
      let barWidth = 30;
      let margin = { top: 0, right: 0, bottom: 0, left: 45 };
      let width = widgetWidth - margin.left - margin.right;
      let height = widgetHeight - margin.top - margin.bottom;
      let xScale = d3.scale.ordinal();
      let yScale = d3.scale.linear();
      let currentBarPadding = 0.1;
      let currentOuterPadding = 0;
      let brush = d3.svg
        .brush()
        .x(xScale)
        .on('brushstart', brushstart)
        .on('brush', brushmove)
        .on('brushend', brushend);
      let groupKey = function(d) {
        return d.group;
      };

      function chart(selection) {
        selection.each(function(data) {
          if (!data) {
            return;
          }
          if (filterStatus === 'FILTERS_RESET') {
            d3
              .select(this)
              .select('.brush')
              .transition()
              .call(brush.extent(brushExtent))
              .call(brush.event);
          }
          width = data.length * barWidth;
          height = widgetHeight - margin.top - margin.bottom;

          updateAxisScales(data);

          // Select the svg element, if it exists.
          svg = d3
            .select(this)
            .selectAll('svg')
            .data([data]);

          const svgEnter = svg
            .enter()
            .append('svg')
            .attr('width', '100%')
            .attr('height', '100%');

          const gEnter = svgEnter.append('g').attr('class', 'main-group');

          const defs = gEnter.append('defs');

          defs
            .append('pattern')
            .attr('id', 'year-bar-stripes')
            .attr('x', 0)
            .attr('y', 0)
            .attr('width', 2)
            .attr('height', 2)
            .attr('patternUnits', 'userSpaceOnUse')
            .attr('patternTransform', 'rotate(30)')
            .append('rect')
            .attr('x', 0)
            .attr('y', 0)
            .attr('width', 2)
            .attr('height', 1)
            .style('stroke', 'none')
            .style('fill', '#505050');

          defs
            .append('pattern')
            .attr('id', 'year-brush-stripes')
            .attr('x', 0)
            .attr('y', 0)
            .attr('width', 2)
            .attr('height', 2)
            .attr('patternUnits', 'userSpaceOnUse')
            .attr('patternTransform', 'rotate(30)')
            .append('rect')
            .attr('x', 0)
            .attr('y', 0)
            .attr('width', 2)
            .attr('height', 1)
            .style('stroke', 'none')
            .style('fill', '#78C9D1');

          gEnter.append('g').attr('class', 'bars-group');

          const brushEnter = gEnter
            .append('g')
            .attr('class', 'brush')
            .call(brush);

          brushEnter
            .selectAll('.resize.w')
            .append('image')
            .attr(
              'xlink:href',
              'data:image/png;base64,',
            )
            .attr('width', 45)
            .attr('height', 40)
            .attr('x', -44);

          brushEnter
            .selectAll('.resize.e')
            .append('image')
            .attr(
              'xlink:href',
              'data:image/png;base64,',
            )
            .attr('width', 45)
            .attr('height', 40);

          gEnter.append('g').attr('class', 'labels-group');

          svg
            .select('g.brush')
            .selectAll('rect')
            .attr('height', height);

          // Update the inner group dimensions.
          const g = svg
            .select('g.main-group')
            .attr(
              'transform',
              'translate(' + margin.left + ',' + margin.top + ')',
            );

          // Update the bars background width
          g
            .select('.bar-background')
            .attr(
              'width',
              width < widgetWidth
                ? widgetWidth - margin.left - margin.right
                : width,
            )
            .attr('height', height);

          bars = g
            .select('.bars-group')
            .selectAll('rect.bar')
            .data(data, groupKey);

          const labels = g
            .select('.labels-group')
            .selectAll('text.label')
            .data(data, groupKey);

          bars
            .enter()
            .append('rect')
            .attr('class', 'bar')
            .attr('width', xScale.rangeBand())
            .attr('height', function(d) {
              return Math.abs(yScale(d.current.count) - yScale(0));
            })
            .attr('x', function(d) {
              return xScale(d.group);
            })
            .attr('y', function(d) {
              return d.current.count < 0 ? yScale(0) : yScale(d.current.count);
            });

          labels
            .enter()
            .append('text')
            .attr('class', 'label')
            .attr('x', function(d) {
              return xScale(d.group) + 3;
            })
            .attr('y', function() {
              return yScale(0) - 5;
            })
            .text(function(d) {
              return '‘' + d.group.substring(2);
            });

          // Remove any unnecessary bar groups
          bars.exit().remove();

          labels.exit().remove();

          setTimeout(function() {
            brushEnter.call(brush.extent(brushExtent)).call(brush.event);
          }, 500);
        });
      }

      function updateAxisScales(data) {
        // Update the scales in case the data has changed
        xScale
          .domain(data.map(groupKey))
          .rangeRoundBands([0, width], currentBarPadding, currentOuterPadding);

        // Update the y-scale domain
        const minMetricValue = d3.min(data, function(d) {
          return d.current.count;
        });

        const absMaxMetricValue = d3.max(data, function(d) {
          return Math.abs(d.current.count);
        });

        const yScaleDomain = [0, absMaxMetricValue];
        if (minMetricValue < 0) {
          yScaleDomain[0] = -absMaxMetricValue;
        }

        yScale
          .range([height, 0])
          .domain(yScaleDomain)
          .nice();
      }

      function brushstart() {
        svg.classed('selecting', true);
      }

      function brushmove() {
        var extent0 = brush.extent(),
          extent1;

        if (d3.event.mode === 'move') {
          const d0 = Math.floor(extent0[0] / barWidth) * barWidth,
            d1 =
              d0 + Math.ceil((extent0[1] - extent0[0]) / barWidth) * barWidth;
          extent1 = [d0, d1];
        } else {
          extent1 = [
            Math.floor(extent0[0] / barWidth) * barWidth,
            Math.ceil(extent0[1] / barWidth) * barWidth,
          ];
        }

        bars.classed('selected', function(d) {
          const x = xScale(d.group);
          return extent1[0] <= x + barWidth / 2 && x <= extent1[1];
        });

        d3.select(this).call(brush.extent(extent1));
      }

      function brushend() {
        var isEmpty = d3.event.target.empty();

        if (isEmpty) {
          var extent = d3.event.target.extent(),
            snappedWidth = Math.floor(extent[0] / barWidth) * barWidth,
            buffer = 3;

          d3
            .select(this)
            .call(
              brush.extent([
                snappedWidth + buffer,
                snappedWidth + barWidth - buffer,
              ]),
            )
            .call(brush.event);
        } else {
          const selectedYears = bars
            .filter('.selected')
            .data()
            .map(function(d) {
              return d.group;
            });

          const changeFilterStatus =
            brush.extent()[0] === brushExtent[0] &&
            brush.extent()[1] === brushExtent[1]
              ? false
              : true;
          onBrushEnd(selectedYears, changeFilterStatus);
        }
      }

      chart.margin = function(_) {
        if (!arguments.length) return margin;
        margin = _;
        width = widgetWidth - margin.left - margin.right;
        height = widgetHeight - margin.top - margin.bottom;
        return chart;
      };

      chart.width = function(_) {
        if (!arguments.length) return widgetWidth;
        widgetWidth = _;
        width = widgetWidth - margin.left - margin.right;
        return chart;
      };

      chart.height = function(_) {
        if (!arguments.length) return widgetHeight;
        widgetHeight = _;
        height = widgetHeight - margin.top - margin.bottom;
        return chart;
      };

      return chart;
    }
  }
}
